/* ================================================================
   Utility  hidden (Tailwind v4 may not scan .ts files for this)
   ================================================================ */

.hidden {
  display: none !important;
}

/* ================================================================
   Scrollbar  slim, theme-aware
   ================================================================ */

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: var(--color-aic-border);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-aic-text-tertiary);
}

/* ================================================================
   Tab bar
   ================================================================ */

.aic-tab {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
  padding: var(--spacing-sm) var(--spacing-md);
  font-family: var(--font-aic);
  font-size: var(--text-sm);
  font-weight: 500;
  color: var(--color-aic-text-secondary);
  background: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  cursor: pointer;
  transition:
    color var(--duration-aic) var(--ease-aic),
    border-color var(--duration-aic) var(--ease-aic);
  white-space: nowrap;
}

.aic-tab:hover {
  color: var(--color-aic-text);
}

.aic-tab--active {
  color: var(--color-aic-blue);
  border-bottom-color: var(--color-aic-blue);
}

.aic-tab--icon {
  padding: var(--spacing-sm);
}

/* ================================================================
   Dropdown menu  proper menu component
   ================================================================ */

.aic-dropdown {
  position: absolute;
  right: 0;
  top: calc(100% + 4px);
  min-width: 200px;
  background: var(--color-aic-bg-elevated);
  border: 1px solid var(--color-aic-border);
  border-radius: var(--radius-md);
  box-shadow:
    0 4px 16px rgba(0, 0, 0, 0.1),
    0 1px 4px rgba(0, 0, 0, 0.06);
  padding: var(--spacing-xs) 0;
  z-index: 20;
  overflow: hidden;
}

.aic-dropdown__item {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  width: 100%;
  padding: var(--spacing-sm) var(--spacing-md);
  font-family: var(--font-aic);
  font-size: var(--text-sm);
  color: var(--color-aic-text);
  background: transparent;
  border: none;
  cursor: pointer;
  transition: background var(--duration-aic) var(--ease-aic);
  text-align: left;
}

.aic-dropdown__item:hover {
  background: var(--color-aic-bg-panel);
}

.aic-dropdown__item:active {
  background: var(--color-aic-blue-light);
}

.aic-dropdown__item svg {
  color: var(--color-aic-text-tertiary);
  flex-shrink: 0;
}

.aic-dropdown__item:hover svg {
  color: var(--color-aic-blue);
}

.aic-dropdown__item--active {
  background: var(--color-aic-blue-light);
  color: var(--color-aic-blue);
  font-weight: 500;
}

.aic-dropdown__item--active svg {
  color: var(--color-aic-blue);
}

.aic-split--open .aic-tab--icon {
  color: var(--color-aic-blue);
  background: var(--color-aic-blue-light);
  border-radius: var(--radius-xs);
}

/* ================================================================
   Buttons
   ================================================================ */

.aic-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-xs);
  padding: 9px var(--spacing-lg);
  border-radius: var(--radius-xs);
  font-family: var(--font-aic);
  font-size: var(--text-sm);
  font-weight: 500;
  cursor: pointer;
  transition: all var(--duration-aic) var(--ease-aic);
  white-space: nowrap;
  border: 1px solid transparent;
  line-height: 1;
}

.aic-btn:active {
