}

.aic-btn:focus-visible {
  outline: 2px solid var(--color-aic-blue);
  outline-offset: 2px;
}

.aic-btn:disabled,
.aic-btn[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}

/* Loading state  inline spinner replaces content */
.aic-btn--loading {
  pointer-events: none;
  position: relative;
  color: transparent !important;
}

.aic-btn--loading::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: 50%;
  left: 50%;
  margin: -8px 0 0 -8px;
  border: 2px solid currentColor;
  border-right-color: transparent;
  border-radius: 50%;
  opacity: 0.8;
  animation: spin 0.6s linear infinite;
}

