.aic-btn--primary.aic-btn--loading::after { color: var(--color-aic-text-inverse); }
.aic-btn--secondary.aic-btn--loading::after { color: var(--color-aic-text-secondary); }
.aic-btn--accent.aic-btn--loading::after { color: var(--color-aic-text-inverse); }

