  color: var(--color-aic-text-inverse);
  border-color: var(--color-aic-blue);
}

.aic-btn--primary:hover {
  background: var(--color-aic-blue-hover);
  border-color: var(--color-aic-blue-hover);
  box-shadow: 0 2px 8px rgba(59, 158, 255, 0.25);
}

