  color: var(--color-aic-text);
  background: var(--color-aic-bg-panel);
}

/* Accent  purple filled, icon-only square */

/* Clear / reset button () for text inputs */
.aic-btn-clear {
  position: absolute;
  top: 6px;
  right: 6px;
  width: 20px;
  height: 20px;
  padding: 0;
