  height: 14px;
  border: 1.5px solid var(--color-aic-border);
  border-radius: 3px;
  margin: 0;
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
  transition: all var(--duration-aic) var(--ease-aic);
