  display: flex;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm) 10px;
  background: var(--color-aic-bg-panel);
  border: 1px solid var(--color-aic-border-light);
  border-radius: var(--radius-sm);
  transition: border-color var(--duration-aic) var(--ease-aic);
}

